/* mv
  ======================================== */

.mv{  
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  background-position: center;
  @include PC {
    height: 745px;
    background-image: url(../img/anne/201811/anne_mv.png);
  }
  @include SP {
    height: calc(100vh - 43px);
    background-image: url(../img/anne/201811/anne_mv_sp.png);
  }
  div{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    &.mv_starring{
      @include PC {margin-top: 170px;}
      @include SP {
        width: 72.5%;
        margin-top: 170px;
      }
    }
    &.mv_title{
      @include PC {margin-top: 30px;}
      @include SP {
        width: 75%;
        margin-top: 70px;
      }
    }
  }
}

/* mainVisual
  ======================================== */

.mainVisual {
  margin-bottom: 80px;
}

.mainVisual--anne {
  .keyMovie_movie {
    video {
      width: 100%;
      vertical-align: top;
      width: 100%;
      vertical-align: top;
    }
    &.end {
      background: url(../img/mainvisual_pc.jpg) no-repeat center center;
      background-size: cover;
    }
  }
  overflow: hidden;
  font-family: 'Ryumin Regular KL', '游明朝', YuMincho, 'ヒラギノ明朝 ProN W3', 'Hiragino Mincho ProN', 'ＭＳ Ｐ明朝', 'ＭＳ 明朝', serif;
  /*  height: 790px;*/
  margin-bottom: 80px;
  position: relative;
  background: #000;
  .bg {
    width: 100%;
    height: 100%;
    background: url(../img/top/anne-bg.png) no-repeat center top;
    position: absolute;
    left: 50%;
    top: 0;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    -webkit-animation-name: animeImage;
    animation-name: animeImage;
    -webkit-animation-duration: 5.5s;
    animation-duration: 5.5s;
    -webkit-animation-timing-function: ease;
    animation-timing-function: ease;
    -webkit-animation-delay: 0s;
    animation-delay: 0s;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1;
    -webkit-animation-direction: normal;
    animation-direction: normal;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
  }
  > .inner {
    position: relative;
    height: 100%;
  }
  .image, .title, .lead {
    -webkit-animation-duration: 5.5s;
    animation-duration: 5.5s;
    -webkit-animation-timing-function: ease;
    animation-timing-function: ease;
    -webkit-animation-delay: 0s;
    animation-delay: 0s;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1;
    -webkit-animation-direction: normal;
    animation-direction: normal;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
  }
  .image {
    -webkit-animation-name: animeImage;
    animation-name: animeImage;
    position: absolute;
    left: 467px;
    top: 102px;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    z-index: 3;
  }
  .textArea {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-writing-mode: vertical-rl;
    -ms-writing-mode: tb-rl;
    writing-mode: vertical-rl;
    transition: -webkit-transform 0.1s ease;
    -webkit-transition: -webkit-transform 0.1s ease;
    transition: transform 0.1s ease;
    transition: transform 0.1s ease, -webkit-transform 0.1s ease;
  }
  .title {
    position: absolute;
    font-size: 140px;
    text-indent: -1.8em;
    line-height: 1.0;
    letter-spacing: 0.06em;
    -webkit-transform: skewY(-11deg);
    transform: skewY(-11deg);
    -webkit-animation-name: animeTitleSp;
    animation-name: animeTitleSp;
  }
  .lead {
    position: absolute;
    padding-top: 2.1em;
    font-size: 70px;
    line-height: 1.5;
    letter-spacing: 0.1em;
    -webkit-animation-name: animeLeadSp;
    animation-name: animeLeadSp;
    span {
      display: block;
      -webkit-transform: skewY(-11deg);
      transform: skewY(-11deg);
    }
  }
  .title {
    -webkit-animation-name: animeTitlePc;
    animation-name: animeTitlePc;
  }
  .lead {
    -webkit-animation-name: animeLeadPc;
    animation-name: animeLeadPc;
  }
}

@media screen and (max-width: 768px) {
  .mainVisual {
    padding-top: 47px;
    margin-bottom: 37px;
  }
  .mainVisual--anne .keyMovie_movie.end {
    background: url(../img/mainvisual_sp.jpg) no-repeat center center;
    background-size: cover;
  }
}

