/* topBlock02
  ======================================== */

.topBlock02 {
  margin-bottom: 103px;
  > .inner {
    width: 984px;
    margin: 0 auto;
  }
  .box {
    width: 882px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    background: #faefe6;
    margin: 0 auto;
    padding: 30px 30px 65px;
  }
  .heading {
    text-align: center;
    margin: -52px 0 48px;
    p {
      width: 172px;
      margin: 0 auto 20px;
    }
    h3 {
      font-size: 2.133rem;
      font-weight: bold;
      line-height: 1;
    }
  }
  .list {
    padding: 0  80px 0 104px;
    margin-bottom: 39px;
    &.showed li {
      &::before {
        opacity: 1;
        background-color: #f48e92;
      }
      &::after {
        opacity: 1;
      }
    }
    li {
      margin-bottom: 32px;
      font-size: 1.2rem;
      line-height: 1.611;
      position: relative;
      padding-left: 46px;
      &:last-child {
        margin-bottom: 0;
      }
      &:nth-child(1) {
        &:before {
          -webkit-transition-delay: 0s,0.5s;
          transition-delay: 0s,0.5s;
        }
        &:after {
          -webkit-transition-delay: 1s;
          transition-delay: 1s;
        }
      }
      &:nth-child(2) {
        &:before {
          -webkit-transition-delay: 0.2s,0.7s;
          transition-delay: 0.2s,0.7s;
        }
        &:after {
          -webkit-transition-delay: 1.2s;
          transition-delay: 1.2s;
        }
      }
      &:nth-child(3) {
        &:before {
          -webkit-transition-delay: 0.4s,0.9s;
          transition-delay: 0.4s,0.9s;
        }
        &:after {
          -webkit-transition-delay: 1.4s;
          transition-delay: 1.4s;
        }
      }
      &:nth-child(4) {
        &:before {
          -webkit-transition-delay: 0.6s,1.1s;
          transition-delay: 0.6s,1.1s;
        }
        &:after {
          -webkit-transition-delay: 1.6s;
          transition-delay: 1.6s;
        }
      }
      &:nth-child(5) {
        &:before {
          -webkit-transition-delay: 0.8s,1.3s;
          transition-delay: 0.8s,1.3s;
        }
        &:after {
          -webkit-transition-delay: 1.8s;
          transition-delay: 1.8s;
        }
      }
      &:nth-child(6) {
        &:before {
          -webkit-transition-delay: 1s,1.5s;
          transition-delay: 1s,1.5s;
        }
        &:after {
          -webkit-transition-delay: 2s;
          transition-delay: 2s;
        }
      }
      &:nth-child(7) {
        &:before {
          -webkit-transition-delay: 1.2s,1.7s;
          transition-delay: 1.2s,1.7s;
        }
        &:after {
          -webkit-transition-delay: 2.2s;
          transition-delay: 2.2s;
        }
      }
      &::before {
        content: "";
        display: block;
        width: 29px;
        height: 29px;
        background: #ddd;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        -webkit-transition: opacity 0.5s 0.3s,background-color 0.5s 0.8s;
        transition: opacity 0.5s 0.3s,background-color 0.5s 0.8s;
      }
      &::after {
        content: "";
        display: block;
        width: 22px;
        height: 17px;
        background: url(../img/top/top_contents_ico01.png) no-repeat 0 0;
        background-size: 100% auto;
        position: absolute;
        top: 6px;
        left: 3px;
        opacity: 0;
        -webkit-transition: all 0.5s 1.3s;
        transition: all 0.5s 1.3s;
      }
    }
  }
  .text {
    text-align: center;
    font-size: 1.689rem;
    font-weight: bold;
  }
}

